
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: "PingFang SC", "STHeitiSC-light", "Helvetica-lighn", Arial;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: '';
  height: 0;
  line-height: 0;
  clear: both;
}
@media (-webkit-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media (-webkit-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.border-1px::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
@font-face {
  font-family: 'icomoon';
  src: url('../../common/fonts/icomoon.eot?hrkgfy');
  src: url('../../common/fonts/icomoon.eot?hrkgfy#iefix') format('embedded-opentype'), url('../../common/fonts/icomoon.ttf?hrkgfy') format('truetype'), url('../../common/fonts/icomoon.woff?hrkgfy') format('woff'), url('../../common/fonts/icomoon.svg?hrkgfy#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-add_circle:before {
  content: "\e900";
}
.icon-arrow_lift:before {
  content: "\e901";
}
.icon-check_circle:before {
  content: "\e902";
}
.icon-close:before {
  content: "\e903";
}
.icon-favorite:before {
  content: "\e904";
}
.icon-keyboard_arrow_right:before {
  content: "\e905";
}
.icon-remove_circle_outline:before {
  content: "\e906";
}
.icon-shopping_cart:before {
  content: "\e907";
}
.icon-thumb_down:before {
  content: "\e908";
}
.icon-thumb_up:before {
  content: "\e909";
}
.ratingselect .rating-type {
  padding: 0 18px 18px 18px;
  margin: 18px 0 0 0 ;
  position: relative;
  font-size: 0;
}
.ratingselect .rating-type::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  width: 100%;
  content: ' ';
}
.ratingselect .rating-type .block {
  display: inline-block;
  padding: 8px 12px;
  border-radius: 2px;
  margin-right: 8px;
  font-size: 12px;
  color: #4d555d;
}
.ratingselect .rating-type .block.active {
  color: #fff;
}
.ratingselect .rating-type .block .count {
  font-size: 8px;
  margin-left: 2px;
  line-height: 16px;
}
.ratingselect .rating-type .block.positive {
  background: rgba(0, 160, 220, 0.2);
}
.ratingselect .rating-type .block.positive.active {
  background: #00a0dc;
}
.ratingselect .rating-type .block.negative {
  background: rgba(77, 85, 93, 0.2);
}
.ratingselect .rating-type .block.negative.active {
  background: #4d555d;
}
.ratingselect .rating-switch {
  line-height: 24px;
  padding: 12px 18px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  color: #93999f;
  font-size: 0;
}
.ratingselect .rating-switch.on .icon-check_circle {
  color: #00c850;
}
.ratingselect .rating-switch .icon-check_circle {
  font-size: 24px;
  margin-right: 4px;
  display: inline-block;
  vertical-align: top;
}
.ratingselect .rating-switch .text {
  font-size: 12px;
}



