
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: "PingFang SC", "STHeitiSC-light", "Helvetica-lighn", Arial;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: '';
  height: 0;
  line-height: 0;
  clear: both;
}
@media (-webkit-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media (-webkit-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.border-1px::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
@font-face {
  font-family: 'icomoon';
  src: url('../../common/fonts/icomoon.eot?hrkgfy');
  src: url('../../common/fonts/icomoon.eot?hrkgfy#iefix') format('embedded-opentype'), url('../../common/fonts/icomoon.ttf?hrkgfy') format('truetype'), url('../../common/fonts/icomoon.woff?hrkgfy') format('woff'), url('../../common/fonts/icomoon.svg?hrkgfy#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-add_circle:before {
  content: "\e900";
}
.icon-arrow_lift:before {
  content: "\e901";
}
.icon-check_circle:before {
  content: "\e902";
}
.icon-close:before {
  content: "\e903";
}
.icon-favorite:before {
  content: "\e904";
}
.icon-keyboard_arrow_right:before {
  content: "\e905";
}
.icon-remove_circle_outline:before {
  content: "\e906";
}
.icon-shopping_cart:before {
  content: "\e907";
}
.icon-thumb_down:before {
  content: "\e908";
}
.icon-thumb_up:before {
  content: "\e909";
}
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.move-enter {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.move-enter-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food.move-leave {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.move-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food .ball-container .ball {
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
  -webkit-transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
  transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
.food .ball-container .ball .inner {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #00a0dc;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.food .food-content .image-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
}
.food .food-content .image-header img {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
}
.food .food-content .image-header .back {
  position: absolute;
  top: 0;
  left: 0;
}
.food .food-content .image-header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food .food-content .content {
  position: relative;
  padding: 18px;
}
.food .food-content .content .title {
  font-size: 14px;
  color: #07111b;
  font-weight: 700;
  line-height: 14px;
  margin-bottom: 8px;
}
.food .food-content .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  font-size: 0;
}
.food .food-content .content .detail .sell-count,
.food .food-content .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.food .food-content .content .detail .sell-count {
  margin-right: 12px;
}
.food .food-content .content .price {
  font-weight: 700;
  line-height: 24px;
}
.food .food-content .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.food .food-content .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.food .food-content .content .cartcontrol-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food .food-content .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background: #00a0dc;
}
.food .food-content .info {
  padding: 18px;
}
.food .food-content .info .title {
  line-height: 14px;
  margin-bottom: 16px;
  font-size: 14px;
  color: #07111b;
  font-weight: 700;
}
.food .food-content .info .text {
  line-height: 24px;
  font-size: 12px;
  padding: 0;
  color: #4d555d;
}
.food .food-content .rating {
  padding-top: 18px;
}
.food .food-content .rating .title {
  line-height: 14px;
  margin-left: 18px;
  font-size: 14px;
  color: #07111b;
  font-weight: 700;
}
.food .food-content .rating .rating-wrapper {
  padding: 0 18px;
}
.food .food-content .rating .rating-wrapper .rating-item {
  padding: 16px 0;
  position: relative;
}
.food .food-content .rating .rating-wrapper .rating-item::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  width: 100%;
  content: ' ';
}
.food .food-content .rating .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  font-size: 0;
  line-height: 12px;
}
.food .food-content .rating .rating-wrapper .rating-item .user .name {
  display: inline-block;
  font-size: 10px;
  vertical-align: top;
  color: #93999f;
  margin-right: 6px;
}
.food .food-content .rating .rating-wrapper .rating-item .user .avatar {
  width: 10px;
  border-radius: 50%;
}
.food .food-content .rating .rating-wrapper .rating-item .time {
  margin-bottom: 6px;
  font-size: 10px;
  line-height: 12px;
  color: #93999f;
}
.food .food-content .rating .rating-wrapper .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_up,
.food .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_down {
  line-height: 16px;
  margin-right: 4px;
  font-size: 12px;
  color: #00a0dc;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_down {
  color: #93999f;
}
.food .food-content .rating .rating-wrapper .no-ratings {
  color: #93999f;
  font-size: 12px;
  padding: 16px 0;
}



